.main-nav__toggle {
  position: absolute;
  top: 6px;
  right: -55px;
  cursor: pointer;
  border: 0;
  padding: 0;
  background: none;
}
.main-nav__toggle:hover, .main-nav__toggle:active {
  background: #f0ebff;
}
