.catalog__checkbox-ios {
  display: inline-block;
  height: 24px;
  line-height: 28px;
  margin-right: 10px;
  position: relative;
  vertical-align: middle;
  font-size: 14px;
  user-select: none;
}

.catalog__checkbox-ios .catalog__checkbox-ios-switch {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  width: 42px;
  height: 24px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 25%/50%;
  vertical-align: top;
  background: rgba(189, 189, 189, 0.7411764706);
  transition: 0.2s;
  margin-right: 4px;
}
.catalog__checkbox-ios .catalog__checkbox-ios-switch:hover {
  border: 2px solid #9070ec;
  border-radius: 12px;
  background: rgba(120, 89, 207, 0.25);
}
.catalog__checkbox-ios .catalog__checkbox-ios-switch:active {
  background: #593cac;
}
.catalog__checkbox-ios .catalog__checkbox-ios-switch:disabled {
  background: #bdbdbd;
}

.catalog__checkbox-ios .catalog__checkbox-ios-switch:before {
  content: "";
  position: absolute;
  top: 1px;
  left: 2px;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: white;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);
  transition: 0.15s;
}

.catalog__checkbox-ios input[type=checkbox] {
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.catalog__checkbox-ios input[type=checkbox]:not(:disabled):active + .catalog__checkbox-ios-switch:before {
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.3);
}

.catalog__checkbox-ios input[type=checkbox]:checked + .catalog__checkbox-ios-switch {
  background: #7859cf;
}

.catalog__checkbox-ios input[type=checkbox]:checked + .catalog__checkbox-ios-switch:before {
  transform: translateX(16px);
}

/* Hover */
.catalog__checkbox-ios input[type=checkbox]:not(:disabled) + .catalog__heckbox-ios-switch {
  cursor: pointer;
  border-color: rgba(0, 0, 0, 0.3);
}

/* Disabled */
.catalog__checkbox-ios input[type=checkbox]:disabled + .catalog__checkbox-ios-switch {
  filter: grayscale(70%);
  border-color: rgba(0, 0, 0, 0.1);
}

.catalog__checkbox-ios input[type=checkbox]:disabled + .catalog__checkbox-ios-switch:before {
  background: #eee;
